.nav {
  display: flex;
  align-items: center;

  @include vp-767 {
    padding-left: 10px;
    align-items: flex-start;
  }
}

.nav__list {
  display: flex;
  gap: 33px;
  flex-wrap: wrap;
  padding-bottom: 5px;

  @include vp-1023 {
    gap: 23px;
    padding: 0 2px 0 0;
  }

  @include vp-767 {
    display: none;
    gap: 26px;
  }
}

.nav__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    opacity: 0.7;
    outline: none;
  }

  &:active {
    opacity: 0.5;
  }
}

.is-open .nav__list {
  @include vp-767 {
    display: flex;
    flex-direction: column;
  }
}
